.main-header {
   width: 100%;
   position: fixed;
   top: 0;
   left: 0;
   background: #1C5476;
   padding: 0.5rem 1rem;
   z-index: 1;
   display: flex;
 }
 
 .main-header > div {
   display: flex;
   vertical-align: middle;
 }
 
.toggle-button {
  width: 4rem;
  background: transparent;
  border: none;
  cursor: pointer;
  padding-top: 0;
  padding-bottom: 0;
  vertical-align: middle;
  margin-right: 25px;
}
 
.toggle-button:focus {
  outline: none;
}
 
.toggle-button__bar {
  width: 100%;
  height: 0.2rem;
  background: white;
  display: block;
  margin: 0.6rem 0;
}
 
.main-nav {
  display: none;
  text-align: right;
  width: calc(100% - 122px);
  vertical-align: middle;
}
 
.main-nav__items {
  margin: 0;
  padding: 0;
  list-style: none;
}
 
.main-nav__item {
  display: inline-block;
  margin: 0 1rem;
}

 
.main-nav__item p,
.mobile-nav__item p {
  text-decoration: none;
  color: white;
  font-weight: bold;
  padding: 0.2rem 0;
}
 


@media (min-width: 40rem) {
  .main-nav {
    display: inline-block;
  }
}

 

 